<template lang="pug">
	.wrapper-created-task
		.overlay(@click="close")
		button.button-trash
		.group
			.users-task
				we-userThumb.master(:data="masteruser")
				we-userThumb(v-for="user in usersShare", :data="user", v-if="!user.master")
				we-buttonShareUser
					
			.created
				we-shareUser(@selectUsers="getUsersShare")
				
				.content-created
					we-editable.title(
						:content="contentTask.title",
						@update="contentTask.title = $event", 
						placeholder="O que  pra fazer?"
					)
					we-editable.descrition(
						:content="contentTask.description",
						@update="contentTask.description = $event", 
						placeholder="Descrio.."
					)

					.inserts(v-for="(child, index) in inserts")
						component(:is="child.name", :key="index", @return="dataInsert")

					.files-uploads(v-if="files.length")
						.header-uploads
							h3.title Arquivos anexados

						.list-files
							.file(v-for="file in files", @click="remove(file)")
								img.image(:src="file.dataURL")

								.details
									span.filename {{file.name}}
									span.size {{file.size}}
									
					.buttons
						button.button-list(@click="editorTask('list')") Lista
						button.button-checklist(@click="editorTask('checklist')") Lista
						button.button-image Lista
				
				.footer-created
					.deadlines
						button.button.button-today(@click="deadline(1, $event.target)").active Hoje
						button.button.button-tomorrow(@click="deadline(2, $event.target)") Amanh
						button.wrapper-datepicker(ref="wrapperDatepicker")
							.overlay
							.placeholder Abrir calendario

							flat-pickr(class="button-date", @on-change="changePickerDate", @on-open="openPickerDate", @on-close="closePickerDate", v-model="contentTask.date", :config="configPicker", placeholder="Abrir calendario")

					button.button-save(:class="activeButtons ? 'active' : ''", @click="saveTask") Salvar em minhas tarefas
					button.button-cancel(:class="activeButtons ? 'active' : ''") Cancelar

</template>

<script>
	import MasterUser from '@/master-user.js'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'
	import flatPickr from 'vue-flatpickr-component';
	import { Portuguese } from "flatpickr/dist/l10n/pt"

	import 'flatpickr/dist/flatpickr.css';
	import Dropzone from 'dropzone'
	import WeChecklist from '@/components/tools-created/We-CheckList.vue'
	import WeList from '@/components/tools-created/We-List.vue'
	import WeUpload from '@/components/tools-created/We-Upload.vue'
	import Boards from '@/components/boards/boards.js'
	import WeButtonShareUser from '@/components/common/We-ButtonShareUser.vue'

	import Files from '@/components/tools-created/files.js'

	export default {
		name: 'w-created-task',
		components:{
			'we-buttonShareUser': WeButtonShareUser,
			'flat-pickr': flatPickr,
			'we-editable': WeEditable,
			'we-shareUser': WeShareUser,
			'we-userThumb': WeUserThumb,
			'we-checklist': WeChecklist,
			'we-list': WeList,
			'we-upload': WeUpload
		},
		data () {
			return {
				inserts: [],
				files: [],
				currentBoard: {
					id: null, 
					title: null
				},
				configPicker: {
					dateFormat: "d/m/Y",
					altFormat: "d M Y",
					altInput: true,
					// allowInput: true,
					locale: Portuguese
				},
				tempDateTaks: null,
				boards: Boards.boards,
				currentTaskCreated: null,
				usersShare: [],
				myDropzone: null,
				myPicker: null,
				activeButtons: false,
				contentTask: {
					title: null,
					description: null,
					list: [],
					date: null,
					checklist: [],
					users: []
				},
				masteruser: MasterUser,
				msg: 'Welcome to Your Vue.js App'
			}
		},
		watch:{
			'contentTask.title'(old, current){
				this.checkInputs(old);
			},
			'contentTask.description'(old, current){
				this.checkInputs(old);
			}
		},
		mounted(){
			this.upload();
			this.datePicker();
			this.getBoard(this.$route.params.id);
		},
		created(){
			this.setDateDafult();
		},
		methods:{
			datePicker(){
				this.myPicker = new flatpickr('#wrapper-datepicker', {});
			},
			openPickerDate(){
				let buttons = document.querySelectorAll('.deadlines .button')
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active');
				}

				let today = new Date();
				this.contentTask.date = today;

				this.$refs.wrapperDatepicker.classList.add('active','show');
			},
			changePickerDate(){
				
			},
			closePickerDate(){
				this.$refs.wrapperDatepicker.classList.remove('show');
			},
			getUsersShare(data){
				this.usersShare = data;
			},
			getBoard(id){
				let tempBoardId = id;

				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == tempBoardId) {
						this.currentBoard.id = tempBoardId;
					}
				}
			},
			arrayMax(arr) {
				return arr.reduce(function (p, v) {
					return ( p > v ? p : v );
				});
			},
			flatMerge(){
			    return Array.prototype.reduce.call(arguments, function (result, current) {
			        if (!(current instanceof Array)) {
			            if (result.indexOf(current) === -1) {
			                result.push(current);
			            }
			        } else {
			            current.forEach(function (value) {
			                if (result.indexOf(value) === -1) {
			                    result.push(value);
			                }
			            });
			        }
			        return result;
			    }, []);
			},
			saveTask(){
				let tempCurrentBoard = null;
				let tempIdNewTask = null;
				let allTasks = [];
				let mergeArrays = null;
				let maxIdTasks = 0;
				let tempArrayTasksIds = [];

				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == this.currentBoard.id) {
						tempCurrentBoard = this.boards[i];

						for (var i = 0; i < tempCurrentBoard.cols.length; i++) {
							allTasks.push(tempCurrentBoard.cols[i].tasks);
						}
					}
				}

				mergeArrays = [].concat.apply([], allTasks);

				for (var i = 0; i < mergeArrays.length; i++) {
					tempArrayTasksIds.push(Number(mergeArrays[i].id))
				}

				maxIdTasks = this.arrayMax(tempArrayTasksIds);

				let tempTask = {
					id: maxIdTasks + 1,
					users: [
						{
							id: 1,
							master: true,
							infos: {
								photo: 'https://randomuser.me/api/portraits/women/12.jpg',
								name: 'vital'
							}
						}
					],
					content: {
						title: this.contentTask.title,
						description: this.contentTask.description,
						date: this.contentTask.date,
						list: this.contentTask.list,
						checklist: this.contentTask.checklist,
						comments: 0,
						files: this.files
					},
					comments:[]
				};

				for (var i = 0; i < this.usersShare.length; i++) {
					tempTask.users.push(this.usersShare[i])
				}
				
				tempCurrentBoard.cols[0].tasks.unshift(tempTask);
				this.close();
			},
			dataInsert(type, data){
				this.contentTask[type] = data;
			},
			editorTask(tollType){
				let temp = {
					name: 'we-'+tollType
				}

				this.inserts.push(temp);
			},
			upload(){
				this.myDropzone = new Dropzone(".button-image", {
					url: 'https://appdividend.com/',
					previewsContainer: false,
					accept: (file, done) => {
						this.files = this.myDropzone.files;
					}
				});
			},
			remove(file){
				this.myDropzone.removeFile(file);
				this.files = this.myDropzone.files;
			},
			checkInputs(old){
				if (old != '') {
					this.activeButtons = true;
				}else{
					this.activeButtons = false;
				}
			},
			close(){
				this.$router.go(-1)
			},
			setDateDafult(){
				let today = new Date();
				let dd = today.getDate();
				let mm = today.getMonth()+1; //January is 0!
				let yyyy = today.getFullYear();

				this.contentTask.date = dd+'/'+mm+'/'+yyyy;
			},
			deadline(type, el){
				let today = new Date();
				let dd = today.getDate();
				let mm = today.getMonth()+1; //January is 0!
				let yyyy = today.getFullYear();

				let buttons = document.querySelectorAll('.deadlines .button')
				this.$refs.wrapperDatepicker.classList.remove('active');

				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active')
				}

				if (type === 1) {
					this.contentTask.date = dd+'-'+mm+'-'+yyyy;
				}else{
					today.setDate(today.getDate()+1);
					dd = today.getDate();
					mm = today.getMonth()+1; //January is 0!
					yyyy = today.getFullYear();
					this.contentTask.date = dd+'-'+mm+'-'+yyyy;
				}

				el.classList.add('active');
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/created-task/CreatedTask.styl"></style>


