<template lang="pug">
    .content-task
        .overlay(@click="close")
        .modal-task
            we-shareUser(:data="currentTask")

            .group
                .header-content
                    .users
                        we-userThumb(v-for="user in currentTask.users", :data="user", :class="user.master ? 'master' : ''")
                        //- we-userThumb(v-if="showTempUsers", v-for="user in usersShare", :data="user")
                        we-buttonShareUser

                    .wrapper-options(ref="options")
                        button.button-options(@click="toggleOptions") Opes
                        button.button-back(@click="toggleOptions")
                        
                        .buttons
                            button.button-hashtag Hashtags
                            button.button-arquivos Aquivos
                            button.button-lembrete Lembretes

                            .more
                                button.button-more Mais Opes

                                .buttons-more
                                    button.button-item Duplicar
                                    button.button-item Excluir

                .content-text
                    .content-title
                        we-editable(
                            class="title",
                            :content="currentTask.content.title",
                            @update="currentTask.content.title = $event"
                        )

                        .buttons
                            button.button-add Salvar
                            button.button-cancel Cancelar

                    .content-description(v-if="currentTask.content.description")
                        we-editable(
                            class="description",
                            :content="currentTask.content.description",
                            @update="currentTask.content.description = $event"
                        )

                        .buttons
                            button.button-add Salvar
                            button.button-cancel Cancelar

                    .timeline
                        span.current-date {{currentTask.content.date}}
                        .others
                            span.date Criado as dia 21/2018 hs 8h
                    
                    .inserts
                        .files-uploads(v-if="currentTask.content.files.length")
                            .header-uploads
                                h3.title Arquivos anexados

                            .list-files
                                .file(v-for="file in currentTask.content.files")
                                    img.image(:src="file.dataURL")
                                    .details
                                        span.filename {{file.name}}
                                        span.size {{file.size}}

                        .list(v-if="Object.keys(currentTask.content.list).length")
                            .header
                                h3.title {{currentTask.content.list.title}}
                            .items
                                .item(v-for="item in currentTask.content.list.items") 
                                    span.text {{item.text}}

                        .checklist(v-if="Object.keys(currentTask.content.checklist).length")
                            .header
                                h3.title {{currentTask.content.checklist.title}}

                            .items
                                .item(v-for="item in currentTask.content.checklist.items") 
                                    span.text {{item.text}}

                .comments(v-if="currentTask.comments")
                    .comment(
                        v-for="(comment, index) in currentTask.comments", 
                        :class="checkUserMaster(comment) ? 'master' : ''", 
                        :key="index")
                        .button-like
                            .emojis
                                button.button-emoji
                                ul.list
                                    li.emoji-1
                                    li.emoji-2
                                    li.emoji-3
                                    li.emoji-4
                                    li.emoji-5

                            .like Curtir

                        .info-user(v-if="!checkUserMaster(comment)")
                            .photo(:style="{'background-image': 'url('+getUserComment(comment.user, 'photo')+')'}")
                            .name {{getUserComment(comment.user, 'name')}}
                            .date-created {{comment.date}}

                        .text
                            .images(v-for="file in comment.files")
                                img.image(:src="file.dataURL")

                            p(v-html="comment.text")

            .footer
                .add-comment(@click="showComment", ref="addComment")
                    we-editable(
                        ref="textComment",
                        :content="contentComment.text",
                        @update="contentComment.text = $event", 
                        placeholder="Adicionar comentario"
                    )
                    
                    .tools
                        button.button-image
                    
                    .files-uploads(v-if="files.length")
                        .header-uploads
                            h3.title Arquivos anexados

                        .list-files
                            .file(v-for="file in files", @click="remove(file)")
                                img.image(:src="file.dataURL")

                                .details
                                    span.filename {{file.name}}
                                    span.size {{file.size}}

                    .buttons
                        button.button-add(@click="saveComment", :class="activeButtons ? 'active' : ''") Comentar
                        button.button-cancel(@click.stop="closeComment") Cancelar

                button.button-concluir-task Concluir Tarefa
</template>

<script>
    import Boards from '@/components/boards/boards.js'
    import WeEditable from '@/components/common/We-Editable.vue'
    import WeShareUser from '@/components/common/We-ShareUser.vue'
    import WeUserThumb from '@/components/common/We-UserThumb.vue'
    import WeButtonShareUser from '@/components/common/We-ButtonShareUser.vue'
    import MasterUser from '@/master-user.js'
    import Users from '@/users.js'

    import Dropzone from 'dropzone'

    export default {
        name: 'we-contentTask',
        components:{
            'we-editable': WeEditable,
            'we-buttonShareUser': WeButtonShareUser,
            'we-shareUser': WeShareUser,
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                files: [],
                inserts: [],
                activeButtons: false,
                users: Users.users,
                masterUser: MasterUser,
                myDropzone: null,
                currentTask: [],
                usersShare: [],
                contentComment: {
                    user: null,
                    files: null,
                    text: null
                },
                msg: 'Welcome to Your Vue.js App'
            }
        },
        watch:{
            'contentComment.text'(old, current){
                this.checkInputs(old);
            }
        },
        mounted(){
            this.upload();
            console.log(this.currentTask.content.list);
        },
        created(){
            this.getTask();
        },
        methods:{
            close(){
                this.$router.go(-1);
                this.saveTask();
            },
            editorTask(tollType){
                let temp = {
                    name: 'we-'+tollType
                }

                this.inserts.push(temp);
            },
            upload(){
                this.myDropzone = new Dropzone(".button-image", {
                    url: 'https://appdividend.com/',
                    previewsContainer: false,
                    accept: (file, done) => {
                        this.files = this.myDropzone.files;
                    }
                });
            },
            dataInsert(type, data){
                this.contentTask[type] = data;
            },
            saveTask(comment){
                for (var i = 0; i < this.usersShare.length; i++) {
                    this.currentTask.users.push(this.usersShare[i])
                }
            },
            clearInputComment(){
                let temp = {
                    user: null,
                    files: null,
                    text: ''
                };

                this.files = [];
                this.activeButtons = false;
                this.contentComment = temp;
                this.$refs.textComment.$el.innerHTML = '';
            },
            checkInputs(old){
                if (old != '') {
                    this.activeButtons = true;
                }else{
                    this.activeButtons = false;
                }
            },
            getUserComment(id, type){
                for (var i = 0; i < this.users.length; i++) {
                    if (this.users[i].id === id) {
                        return this.users[i].infos[type];
                    }
                }
            },
            showComment(){
                this.$refs.addComment.classList.add('show');
            },
            closeComment(){
                this.$refs.addComment.classList.remove('show');
                this.clearInputComment();
            },
            checkUserMaster(comment){
                if (this.masterUser.id === comment.user) {
                    return true
                }
            },
            saveComment(){
                let tempComment = {
                    user: this.masterUser.id,
                    text: this.contentComment.text,
                    files: this.files
                }

                this.currentTask.comments.push(tempComment);
                this.clearInputComment();
            },
            getTask(){
                let currentBoard = null;
                let currentCol = null;

                let idBoard = this.$route.params.idBoard;
                let idCol = this.$route.params.idCol;
                let idTask = this.$route.params.idTask;

                for (var i = 0; i < Boards.boards.length; i++) {
                    currentBoard = Boards.boards[i];

                    if (currentBoard.id == idBoard) {
                        for (var i = 0; i < currentBoard.cols.length; i++) {
                            currentCol = currentBoard.cols[i]
                            if (currentCol.id == idCol) {
                                for (var i = 0; i < currentCol.tasks.length; i++) {
                                    if (currentCol.tasks[i].id == idTask) {
                                        this.currentTask = currentCol.tasks[i];
                                    }
                                }
                            }
                        }
                    }
                }
            },
            toggleOptions(){
                this.$refs.options.classList.toggle('show-options');
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/common/modais/ContentTask.styl"></style>


