<template lang="pug">
    .note(@click="openNote")
        .images(v-if="data.tools.images.length")
            .image(v-for="item in data.tools.images", :data-size="setSize", :style="{'background-image': 'url('+item.dataURL+')'}")

        .container
            .content-text
                h2.title(v-html="data.title")
                p.description(v-html="data.description")
                
                .list(v-if="Object.keys(data.tools.list).length")
                    .header
                        h3.title {{data.tools.list.title}}
                    .items
                        .item(v-for="item in data.tools.list.items") 
                            span.text {{item.text}}

                .checklist(v-if="Object.keys(data.tools.checklist).length")
                    .header
                        h3.title {{data.tools.checklist.title}}

                    .items
                        .item(v-for="item in data.tools.checklist.items") 
                            span.text {{item.text}}

            .footer
                .date-edit 8 horas atrs

</template>

<script>
    export default {
        name: 'we-note',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        computed:{
            setSize(){
                if (this.data.tools.images.length === 1) {
                    return 1
                }else if(this.data.tools.images.length === 2){
                    return 2
                }else if(this.data.tools.images.length >= 3){
                    return 3
                }
            }
        },
        mounted(){
            
        },
        methods: {
            openNote(){
                let checkDrop = this.$el.getAttribute('data-drag');
                
                if (!checkDrop) {
                    this.$router.push({
                        name: 'note',
                        params: {
                            idNote: this.data.id
                        }
                    })
                }
            }
        },
        props: ['data']
    }
</script>
<style lang="stylus" scope src="@/assets/styles/notes/Note.styl"></style>


